import { onMounted, watch } from "vue";
import { ElMessage, ElMessageBox } from "element-plus";
import { List as icon_list, Edit as icon_edit, Search as icon_search, Operation as icon_operation } from "@element-plus/icons-vue";
import commonFn from "@/hooks/index";
import usersFn from "@/hooks/pages/users";
export default {
  setup() {
    // 以下：頁面共用組件
    const {
      pageSize,
      currPage,
      searchMap,
      mode,
      dialogFormVisible,
      handleCurrentChange,
      handleSizeChange,
      hasPermission,
      userInfo
    } = commonFn(); // 以下：該頁組件切割

    const {
      store,
      listLoading,
      list,
      statusTags,
      rolesChecked,
      fullList,
      rules,
      form,
      formRef,
      clearForm,
      pages,
      checkAll,
      isIndeterminate,
      checkedPage,
      handleCheckAllChange,
      handleCheckedPagesChange
    } = usersFn(); // 以下：表單模式切換

    const Add = () => {
      if (mode.value !== "add") {
        mode.value = "add";
        Clear();
      }

      dialogFormVisible.value = true;
    };

    const Clear = () => {
      Object.assign(form, clearForm);
      checkAll.value = checkAll.value.map(() => false);
      isIndeterminate.value = isIndeterminate.value.map(() => false);
      checkedPage.value.forEach((item, index) => {
        if (index === 0) checkedPage.value[index] = ["user:view"];else checkedPage.value[index] = [];
      });
    };

    const Edit = (_id, _mode) => {
      if (mode.value !== _mode) mode.value = _mode;
      const editData = list.value.find(a => a.user_id === _id);
      Object.assign(form, editData);

      if (mode.value === "password") {
        form.passconf = "";
        form.password = "";
      } else {
        form.passconf = "*";
        form.password = "*";
      }

      checkedPage.value = editData.checkedPage;

      if (!editData.roles?.some(d => d.includes("admin"))) {
        checkedPage.value.forEach((item, index) => {
          // 初始化
          checkAll.value[index] = false;
          isIndeterminate.value[index] = false;

          if (item.length > 0) {
            // 判斷哪個需要勾選
            if (item.length === pages[index].all.length) checkAll.value[index] = true;else isIndeterminate.value[index] = true;
          }
        });
      }

      dialogFormVisible.value = true;
    };

    const handleSubmit = formRef => {
      form.roles = getRoles();
      formRef.validate(valid => {
        console.log(valid);

        if (valid) {
          console.log(mode.value);
          if (mode.value === "add") Insert();else if (mode.value === "update") Update();else if (mode.value === "account") UpdateAccount();else if (mode.value === "password") UpdatePassword();
        } else ElMessage({
          message: "請確認表單是否符合格式!",
          type: "error"
        });
      });
    }; // 獲取被勾選的權限角色


    const getRoles = () => {
      if (hasPermission(userInfo.value.roles, "admin")) return ["admin"]; // checkedPage是二維，我們要變一維全部撈出來

      return [].concat.apply([], checkedPage.value);
    }; // Update & Password & Delete Btn顯示邏輯


    const checkRoles = (roles = [], user_id, access) => {
      if (hasPermission(userInfo.value.roles, "admin")) {
        // ADMIN
        if (access === "user:update" || access === "user:account") return true;else return !(user_id === userInfo.value.user_id);
      } else {
        // 普通使用者
        if (access === "user:update" || access === "user:account") return user_id === userInfo.value.user_id || !hasPermission(roles, "admin") && hasPermission(userInfo.value.roles, access);else return !(user_id === userInfo.value.user_id || hasPermission(roles, "admin")) && hasPermission(userInfo.value.roles, access);
      }
    }; // 以下：CRUD
    // mounted後獲取資料


    onMounted(() => {
      fetchData();
    });

    const fetchData = async () => {
      await store.dispatch("users/getUsers");
      Search();
    };

    const afterRequest = res => {
      if (res?.msg) {
        Search();
        ElMessage({
          message: res.msg,
          type: "success"
        });
        dialogFormVisible.value = false;
        if (mode.value === "add") Clear();
      }
    };

    const Insert = async () => {
      const res = await store.dispatch("users/insertUsers", { ...form
      });
      afterRequest(res);
    };

    const Update = async () => {
      const res = await store.dispatch("users/updateUsers", {
        user_id: form.user_id,
        name: form.name
      });
      afterRequest(res);
    };

    const UpdateAccount = async () => {
      const res = await store.dispatch("users/updateUsersAccount", {
        user_id: form.user_id,
        account: form.account,
        status: form.status,
        roles: form.roles
      });
      afterRequest(res);
    };

    const UpdatePassword = async () => {
      const res = await store.dispatch("users/updateUsersPassword", {
        user_id: form.user_id,
        password: form.password,
        passconf: form.passconf
      });
      afterRequest(res);
    };

    const Delete = _id => {
      ElMessageBox.confirm("是否要刪除該筆資料?", "注意", {
        confirmButtonText: "是",
        cancelButtonText: "否",
        type: "warning"
      }).then(async () => {
        const res = await store.dispatch("users/deleteUsers", {
          user_id: _id
        });
        afterRequest(res);
      });
    }; // 監聽搜尋欄位，根據關鍵字來搜尋


    const Search = async () => {
      if (String(searchMap.word) !== "") await store.dispatch("users/filterUsers", { ...searchMap
      });else await store.dispatch("users/noFilterUsers");
    }; // reactive要用 () => $var, ref用$var


    watch(() => searchMap.word, () => {
      if (String(searchMap.word) === "") Search();
    });
    return {
      // ICON
      icon_list,
      icon_edit,
      icon_search,
      icon_operation,
      // DATA
      list,
      statusTags,
      rolesChecked,
      fullList,
      // 控制狀態
      listLoading,
      dialogFormVisible,
      // 分頁
      pageSize,
      currPage,
      handleCurrentChange,
      handleSizeChange,
      // 表單
      form,
      formRef,
      rules,
      // 控制checkbox表單
      pages,
      checkAll,
      isIndeterminate,
      checkedPage,
      handleCheckedPagesChange,
      handleCheckAllChange,
      checkRoles,
      // 提交模式
      mode,
      // 開啟表單
      handleSubmit,
      Add,
      Edit,
      // Update,
      // UpdateAccount,
      // UpdatePassword,
      Delete,
      // 關鍵字
      searchMap,
      Search,
      // 使用者資訊
      userInfo,
      hasPermission
    };
  }

};