import { reactive, ref, computed } from "vue"
import { useStore } from "vuex"
export default function () {
  const validSamePassword = (rule, value, callback) => {
    if (value !== form.password) callback(new Error("密碼需要一致!"))
    else callback()
  }
  const rules = reactive({
    name: [
      { required: true, message: "請輸入名稱!", trigger: "blur" },
      { max: 64, message: "長度不能超過64個字!", trigger: "blur" }
    ],
    account: [
      { required: true, message: "請輸入帳號!", trigger: "blur" },
      { max: 64, message: "長度不能超過64個字!", trigger: "blur" }
    ],
    password: [
      { required: true, message: "請輸入密碼!", trigger: "blur" },
      { max: 256, message: "長度不能超過256個字!", trigger: "blur" }
    ],
    passconf: [
      { required: true, message: "請輸入密碼確認!", trigger: "blur" },
      { max: 256, message: "長度不能超過256個字!", trigger: "blur" },
      {
        validator: validSamePassword,
        trigger: "blur"
      }
    ],
    status: [{ required: true, message: "請選擇狀態!", trigger: "blur" }],
    roles: [{ required: true, message: "請選擇權限!", trigger: "blur" }]
  })

  const formRef = ref(null)

  // form
  const form = reactive({
    user_id: 0,
    name: "",
    account: "",
    password: "",
    passconf: "",
    checkedPage: [],
    roles: [],
    status: 1
  })

  const clearForm = {
    user_id: 0,
    name: "",
    account: "",
    password: "",
    passconf: "",
    checkedPage: [],
    roles: [],
    status: 1
  }

  const store = useStore()
  const list = computed(() => store.getters.users.users)
  const fullList = computed(() => store.getters.users.fullUsers)
  const listLoading = computed(() => store.getters.users.listLoading)

  const statusTags = computed(() =>
    list.value.map((l) =>
      l.status === 1
        ? { type: "success", name: "使用中" }
        : { type: "danger", name: "停用中" }
    )
  )

  const rolesChecked = computed(() =>
    list.value.map((l) => {
      let checked = []
      ;["user", "clinic"].forEach((d) => {
        const temp = l.roles.filter((a) => a.match(d))
        checked.push(temp)
      })
      return checked
    })
  )

  const checkAll = ref([false], [false])
  const isIndeterminate = ref([true], [false])
  const checkedPage = ref([["user:view"], []])

  const pages = [
    {
      name: "使用者管理",
      options: [
        {
          name: "查看",
          value: "user:view"
        },
        {
          name: "新增",
          value: "user:create"
        },
        {
          name: "更新",
          value: "user:update"
        },
        {
          name: "更新帳號",
          value: "user:account"
        },
        {
          name: "更新密碼",
          value: "user:password"
        },
        {
          name: "刪除",
          value: "user:delete"
        }
      ],
      all: [
        "user:view",
        "user:create",
        "user:update",
        "user:account",
        "user:password",
        "user:delete"
      ]
    },
    {
      name: "診所管理",
      options: [
        {
          name: "查看",
          value: "clinic:view"
        },
        {
          name: "新增",
          value: "clinic:create"
        },
        {
          name: "更新",
          value: "clinic:update"
        },
        {
          name: "更新Token",
          value: "clinic:token"
        },
        {
          name: "刪除",
          value: "clinic:delete"
        }
      ],
      all: [
        "clinic:view",
        "clinic:create",
        "clinic:update",
        "clinic:token",
        "clinic:delete"
      ]
    }
  ]

  const handleCheckAllChange = (index) => {
    if (checkAll.value[index]) checkedPage.value[index] = pages[index].all
    else {
      if (index === 0) checkedPage.value[index] = ["user:view"]
      else checkedPage.value[index] = []
    }
    isIndeterminate.value[index] = false
  }
  const handleCheckedPagesChange = (index) => {
    const c = checkedPage.value[index].length
    checkAll.value[index] = c === pages[index].all.length
    isIndeterminate.value[index] = c > 0 && c < pages[index].all.length
  }

  return {
    store,
    listLoading,
    list,
    statusTags,
    rolesChecked,
    fullList,
    rules,
    form,
    formRef,
    clearForm,
    pages,
    checkAll,
    isIndeterminate,
    checkedPage,
    handleCheckAllChange,
    handleCheckedPagesChange
  }
}
